.login{
    background-color: rgb(255, 254, 254);
    display: flex;
    height: 100vh;
    flex-direction: column;
    align-items: center;
}
.login__logo{
    margin-top: 100px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 200px;
    margin-right: auto;
    margin-left: auto;
}
.login__container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid rgb(0, 0, 0);
    padding: 20px;
}

.login__container >h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container >form > h5{
    margin-bottom: 5px;
}
.login__container >form >input {
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 90%;
}
.login__container > p{
    margin-top: 15px;
    font-size: 12px;
}
.login__signInButton{
    background: #f0c14b;
    border: 1px solid;
    width: 100%;
    height: 30px;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: black;
}
.login__registerButton{
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    border-color: darkgray;
    margin-top: 10px;

}